import React, { useCallback, useState } from 'react';

import { TTableRowData } from '../types/table';

import RowForm from '../RowForm';
import Btn from '../Btn';
import AppDialog from '../AppDialog';

import './index.css';

type TProps = {
    close?: () => void;
    submit?: (tableRowData: TTableRowData) => void;
};

const AddDialog = (props: TProps) => {
    const {
        close = () => { },
        submit = (row) => { },
    } = props;

    const [tableRowData, setTableRowData] = useState<TTableRowData>({
        name: '',
        note: '',
        password: '',
        url: '',
        username: '',
    });

    const submitCallback = useCallback(() => {
        submit(tableRowData);
    }, [
        submit,
        tableRowData,
    ]);

    return (
        <AppDialog>
            <div className='AddDialog__title'>
                AddDialog
            </div>

            <RowForm
                data={tableRowData}
                setData={setTableRowData}
            />

            <div className='AddDialog__btn'>
                <Btn
                    text='Cansel'
                    onClick={() => { close(); }}
                />
                <Btn
                    text='Submit'
                    onClick={submitCallback}
                />
            </div>
        </AppDialog>
    );
};

export default AddDialog;
