import React from 'react';

import { TTable, TTableRow } from "../types/table";

import Trow from '../Trow';

import './index.css';

type TProps = {
    table: TTable;
    search: string;
    edit: (data: TTableRow) => void;
};

const Tbody = (props: TProps) => {
    const {
        table,
        search,
        edit,
    } = props;

    return (
        <tbody>
            {table.map((row, index) => {
                const rowId = row.id;
                return (
                    <Trow
                        key={rowId}
                        index={index}
                        row={row}
                        search={search}
                        edit={() => {
                            edit(row);
                        }}
                    />
                );
            })}
        </tbody>
    );
};

export default Tbody;
