import { TTable } from '../types/table';

import { decrypt, encrypt } from '../utils/crypt';
import { tableToText, textToTable } from '../utils/table';

export const getData = async (cryptKey: string): Promise<TTable> => {

    const url = `${process.env.PUBLIC_URL}/data.txt`;

    const response = await fetch(url);

    if (!response.ok) {
        throw new Error(`${response.status}/${response.statusText}`);
    }

    const encryptTextInit = await response.text();

    const decryptText = decrypt(cryptKey, encryptTextInit);

    const table = textToTable(decryptText);

    return table;
};

export const saveData = (cryptKey: string, table: TTable): string => {
    const text = tableToText(table);

    const encryptText = encrypt(cryptKey, text);

    return encryptText;
};
