import React from 'react';

import './index.css';

type TProps = {
    text: string;
    onClick?: () => void;
};

const Btn = (props: TProps) => {
    const {
        text,
        onClick = () => { },
    } = props;

    return (
        <button
            className='Btn'
            onClick={onClick}
        >
            {text}
        </button>
    );
};

export default Btn;
