import React, { useState } from 'react';

import { TTableRow, TTableRowData } from '../types/table';

import Btn from '../Btn';
import RowForm from '../RowForm';
import AppDialog from '../AppDialog';

import './index.css';

type TProps = {
    data: TTableRow;
    close?: () => void;
    update?: (data: TTableRow) => void;
    remove?: (data: TTableRow) => void;
};

const EditDialog = (props: TProps) => {
    const {
        data,
        close = () => { },
        update = () => { },
        remove = () => { },
    } = props;

    const [tableRowData, setTableRowData] = useState<TTableRowData>({
        ...data,
    });

    return (
        <AppDialog>
            <div className='EditDialog__title'>
                EditDialog
            </div>

            <RowForm
                data={tableRowData}
                setData={setTableRowData}
            />

            <div className='EditDialog__btn'>
                <Btn
                    text='Cansel'
                    onClick={() => { close(); }}
                />
                <Btn
                    text='Submit'
                    onClick={() => {
                        update({
                            ...tableRowData,
                            id: data.id,
                        });
                    }}
                />
                <Btn
                    text='Remove'
                    onClick={() => {
                        remove(data);
                    }}
                />
            </div>
        </AppDialog>
    );
};

export default EditDialog;
