import React from 'react';

import './index.css';

const strToHide = (str: string, hide: boolean) => {
    if (hide) {
        const length = str.length;
        return new Array(length).fill('*').join('');
    }
    return str;
};

type TProps = {
    search: string;
    text: string;
    hide: boolean;
};

const SearchData = (props: TProps) => {
    const {
        search,
        text,
        hide,
    } = props;

    const highlight = search !== '' && text.includes(search);

    if (highlight) {
        const arr = text.split(search);

        return (
            <>
                {arr.map((sub, index) => {
                    if (index === 0) {
                        return strToHide(sub, hide);
                    }
                    return (
                        <span key={`${index}`}>
                            <span className='search'>
                                {strToHide(search, hide)}
                            </span>
                            {strToHide(sub, hide)}
                        </span>
                    )
                })}
            </>
        );
    }

    return (
        <>
            {strToHide(text, hide)}
        </>
    );
};

export default SearchData;
