import { useCallback, useEffect, useState } from 'react';

import { TAppState, TSort, TTable, TTableRow } from "../types/table";

type TComparableArrayItem = [
    str: string,
    data: TTableRow,
];

const useTableSort = (tableForSort: TAppState['table'], sort: TSort) => {
    const [table, setTable] = useState<TTable>([]);

    const sortTable = useCallback(async () => {
        if (tableForSort === null) {
            setTable([]);
            return;
        }

        const getValue = (x: TTableRow) => {
            return x[sort.key];
        };

        const comparableArray: TComparableArrayItem[] = await Promise.all(tableForSort.map(async (x) => {
            return [
                getValue(x),
                x,
            ];
        }));

        const retDef = sort.dir === -1 ? 1 : -1;

        comparableArray.sort((a, b) => {
            const str1 = a[0].toUpperCase();
            const str2 = b[0].toUpperCase();

            let ret = retDef;
            if (str1 > str2) {
                ret = sort.dir;
            }

            return ret;
        });

        const sortedArray = comparableArray.map(x => x[1]);

        setTable(sortedArray);
    }, [
        sort,
        tableForSort,
    ]);

    useEffect(() => {
        sortTable();
    }, [
        sortTable,
    ]);

    return table;
};

export default useTableSort;
