import React, { useState, useCallback } from 'react';

import { TDialog, TSort, TTable } from '../types/table';

import Tbody from '../Tbody';
import Btn from '../Btn';
import ThSort from '../ThSort';

import useTablefilter from '../hooks/useTablefilter';
import useTableSort from '../hooks/useTableSort';

import './index.css';

type TProps = {
    table: TTable;
    setShowDialog: (data: TDialog) => void;
    onClickSave?: () => void;
};

const DataTable = (props: TProps) => {
    const {
        table,
        setShowDialog,
        onClickSave,
    } = props;

    const [search, setSearch] = useState('');

    const [sort, setSort] = useState<TSort>({
        key: 'id',
        dir: 1,
    });

    const setSortCallback = useCallback((key: TSort['key']) => {
        setSort((dataSort) => {
            if (dataSort.key === key) {
                const dir = dataSort.dir === 1 ? -1 : 1;
                return {
                    key,
                    dir,
                };
            }
            return {
                key,
                dir: 1,
            };
        });
    }, []);

    const tablefilter = useTablefilter(table, search);

    const tableSort = useTableSort(tablefilter, sort);

    return (
        <div className='DataTable'>
            <table className='table'>
                <colgroup>
                    <col width={60} />
                    <col width={60} />
                    <col />
                    <col />
                    <col />
                    <col />
                    <col />
                </colgroup>
                <thead>
                    <tr>
                        <td colSpan={7}>
                            <div className='DataTable__head'>
                                <div>
                                    <input
                                        type="text"
                                        value={search}
                                        onChange={x => setSearch(x.target.value)}
                                    />
                                </div>
                                {!!onClickSave && (
                                    <div>
                                        <Btn
                                            text='Save'
                                            onClick={onClickSave}
                                        />
                                    </div>
                                )}
                                <div>
                                    <Btn
                                        text='Add'
                                        onClick={() => setShowDialog({
                                            type: 'ADD',
                                        })}
                                    />
                                </div>
                            </div>
                        </td>
                    </tr>
                    <tr>
                        <td></td>
                        <td>
                            <ThSort
                                title='id'
                                sortKey='id'
                                sort={sort}
                                onClick={setSortCallback}
                            />
                        </td>
                        <td>
                            <ThSort
                                title='name'
                                sortKey='name'
                                sort={sort}
                                onClick={setSortCallback}
                            />
                        </td>
                        <td>
                            <ThSort
                                title='url'
                                sortKey='url'
                                sort={sort}
                                onClick={setSortCallback}
                            />
                        </td>
                        <td>
                            <ThSort
                                title='username'
                                sortKey='username'
                                sort={sort}
                                onClick={setSortCallback}
                            />
                        </td>
                        <td>
                            <ThSort
                                title='password'
                                sortKey='password'
                                sort={sort}
                                onClick={setSortCallback}
                            />
                        </td>
                        <td>
                            <ThSort
                                title='note'
                                sortKey='note'
                                sort={sort}
                                onClick={setSortCallback}
                            />
                        </td>
                    </tr>
                </thead>
                <Tbody
                    table={tableSort}
                    search={search}
                    edit={(data) => {
                        setShowDialog({
                            type: 'EDIT',
                            data,
                        })
                    }}
                />
            </table>
        </div>
    );
};

export default DataTable;
