import { TTable } from "../types/table";

const SEPARATER_TR = '|+|';
const SEPARATER_TD = ',';

export const tableToText = (table: TTable) => {
    const text = table.reduce((str, row) => {
        const strRow = Object.values(row).join(',');
        if (str !== '') {
            return `${str}${SEPARATER_TR}${strRow}`;
        }
        return `${strRow}`;
    }, '');

    return text;
};

export const textToTable = (text: string): TTable => {
    const rows = text.split(SEPARATER_TR);

    const table = rows.reduce<TTable>((arr, row, _index) => {

        const [id, name, url, username, password, note] = row.split(SEPARATER_TD);

        return [
            ...arr,
            {
                id,
                name,
                url,
                username,
                password,
                note,
            }
        ];
    }, []);

    return table;
};
