import React, { useEffect, useState, useCallback } from 'react';

import { TDialog } from '../types/table';

import * as storage from '../storage';

import InputKey from '../InputKey';
import AddDialog from '../AddDialog';
import EditDialog from '../EditDialog';
import DataTable from '../DataTable';

import useAppState from '../hooks/useAppState';

import './index.css';

const App = () => {
    const [cryptKey, setCryptKey] = useState('');

    const [showDialog, setShowDialog] = useState<TDialog>(null);

    const { table, addRow, removeRow, updateRow, changedRowsId, setChangedRowsId } = useAppState(cryptKey);

    const isChanged = Object.keys(changedRowsId).length > 0;

    useEffect(() => {
        const beforeUnload = (event: any) => {
            if (isChanged) {
                // Отмените событие, как указано в стандарте.
                event.preventDefault();
                // Chrome требует установки возвратного значения.
                event.returnValue = '';
            }
        };

        window.addEventListener('beforeunload', beforeUnload);

        return () => {
            window.removeEventListener('beforeunload', beforeUnload);
        };
    }, [
        isChanged,
    ]);

    const onClickSaveCallback = useCallback(() => {
        if (table !== null) {
            const cryptKeyData = window.prompt('Key ?');
            if (cryptKeyData !== null) {
                const key = cryptKeyData.trim();

                const data = storage.saveData(key, table);

                setChangedRowsId({});

                console.log(data);
            }
        }
    }, [
        table,
        setChangedRowsId,
    ]);

    const onClickSave = isChanged ? onClickSaveCallback : undefined;

    return (
        <>
            {table === null ? (
                <div className="App">
                    <InputKey
                        submit={setCryptKey}
                    />
                </div>
            ) : (
                <DataTable
                    table={table}
                    setShowDialog={setShowDialog}
                    onClickSave={onClickSave}
                />
            )}

            {showDialog?.type === 'ADD' && (
                <AddDialog
                    close={() => {
                        setShowDialog(null);
                    }}
                    submit={(tableRowData) => {
                        addRow(tableRowData);
                        setShowDialog(null);
                    }}
                />
            )}

            {showDialog?.type === 'EDIT' && (
                <EditDialog
                    data={showDialog.data}
                    close={() => {
                        setShowDialog(null);
                    }}
                    remove={(tableRow) => {
                        const text = window.prompt('Точно удалить? Tipe "yes"');

                        if (text === 'yes') {
                            removeRow(tableRow.id);
                            setShowDialog(null);
                        };
                    }}
                    update={(tableRow) => {
                        updateRow(tableRow);
                        setShowDialog(null);
                    }}
                />
            )}
        </>
    );
};

export default App;
