import React from 'react';

import { TTableRowData } from '../types/table';

import './index.css';

type TProps = {
    data: TTableRowData;
    setData: (data: TTableRowData) => void;
};

const RowForm = (props: TProps) => {
    const {
        data,
        setData,
    } = props;

    return (
        <div className='RowForm'>
            <div>
                <div>
                    <label className='RowForm__label' htmlFor="name">Name</label>
                </div>
                <textarea
                    className='RowForm__textarea'
                    name='name'
                    id='name'
                    value={data.name}
                    onChange={(event) => {
                        const name = event.target.value;
                        setData({
                            ...data,
                            name,
                        });
                    }}
                />
            </div>
            <div>
                <div>
                    <label className='RowForm__label' htmlFor="username">Username</label>
                </div>
                <textarea
                    className='RowForm__textarea'
                    name='username'
                    id='username'
                    value={data.username}
                    onChange={(event) => {
                        const username = event.target.value;
                        setData({
                            ...data,
                            username,
                        });
                    }}
                />
            </div>
            <div>
                <div>
                    <label className='RowForm__label' htmlFor="url">Url</label>
                </div>
                <textarea
                    className='RowForm__textarea'
                    name='url'
                    id='url'
                    value={data.url}
                    onChange={(event) => {
                        const url = event.target.value;
                        setData({
                            ...data,
                            url,
                        });
                    }}
                />
            </div>
            <div>
                <div>
                    <label className='RowForm__label' htmlFor="password">Password</label>
                </div>
                <textarea
                    className='RowForm__textarea'
                    name='password'
                    id='password'
                    value={data.password}
                    onChange={(event) => {
                        const password = event.target.value;
                        setData({
                            ...data,
                            password,
                        });
                    }}
                />
            </div>
            <div>
                <div>
                    <label className='RowForm__label' htmlFor="note">Note</label>
                </div>
                <textarea
                    className='RowForm__textarea'
                    name='note'
                    id='note'
                    value={data.note}
                    onChange={(event) => {
                        const note = event.target.value;
                        setData({
                            ...data,
                            note,
                        });
                    }}
                />
            </div>
        </div>
    );
};

export default RowForm;
