import { useCallback, useState, useEffect } from 'react';

import { TTable, TTableRow, TTableRowData } from "../types/table";

import * as storage from '../storage';

const useAppState = (cryptKey: string) => {
    const [changedRowsId, setChangedRowsId] = useState<Record<string, 1>>({});

    const [table, setTable] = useState<TTable | null>(null);

    const getId = useCallback(() => {
        if (table !== null) {
            const arrIds = table.map(x => +x.id);
            const idNum = Math.max(...arrIds);
            const id = `${idNum + 1}`;

            return id;
        }
        return '1';
    }, [
        table,
    ]);

    const addRow = useCallback((trData: TTableRowData) => {
        if (table !== null) {
            const id = getId();

            const data = [
                ...table,
                {
                    id,
                    ...trData,
                },
            ];

            setTable(data);

            setChangedRowsId((arr) => {
                return {
                    ...arr,
                    [id]: 1,
                };
            });
        }
    }, [
        table,
        getId,
    ]);

    const removeRow = useCallback((rowId: TTableRow['id']) => {
        if (table !== null) {
            const data = table.filter(x => x.id !== rowId);
            setTable(data);
            setChangedRowsId((arr) => {
                return {
                    ...arr,
                    [rowId]: 1,
                };
            });
        }
    }, [
        table,
    ]);

    const updateRow = useCallback((row: TTableRow) => {
        if (table !== null) {
            const data = table.map(x => {
                if (x.id === row.id) {
                    return {
                        ...row,
                    };
                }
                return x;
            });
            setTable(data);

            setChangedRowsId((arr) => {
                return {
                    ...arr,
                    [row.id]: 1,
                };
            });
        }
    }, [
        table,
    ]);

    const initTable = useCallback(async () => {
        if (cryptKey !== '') {
            try {
                const table = await storage.getData(cryptKey);

                setTable(table);
                setChangedRowsId({});
            } catch (error) {
                if (error instanceof Error) {
                    console.log(error.message);
                } else {
                    console.log(error);
                }
            }
        }
    }, [
        cryptKey,
    ]);

    useEffect(() => {
        initTable();
    }, [
        initTable,
    ]);

    useEffect(() => {
        if (table !== null) {
            storage.saveData(cryptKey, table);
        }
    }, [
        table,
        cryptKey,
    ]);

    return {
        table,
        addRow,
        removeRow,
        updateRow,
        changedRowsId,
        setChangedRowsId,
    };
};

export default useAppState;
