import React, { useState } from 'react';

import { TTableRow } from "../types/table";

import DataBlock from '../DataBlock';

import './index.css';

type TProps = {
    index: number;
    row: TTableRow;
    search: string;
    edit: () => void;
};

const Trow = (props: TProps) => {
    const {
        index,
        row,
        search,
        edit,
    } = props;

    const [isMouseOver, setIsMouseOver] = useState(false);

    return (
        <tr
            onMouseOver={() => {
                setIsMouseOver(true);
            }}
            onMouseLeave={() => {
                setIsMouseOver(false);
            }}
        >
            <td>
                {index + 1}
            </td>
            <td>
                <DataBlock
                    isMouseOver={isMouseOver}
                    text={row.id}
                    edit={edit}
                />
            </td>
            <td>
                <DataBlock
                    isMouseOver={isMouseOver}
                    copyed={true}
                    text={row.name}
                    search={search}
                />
            </td>
            <td>
                <DataBlock
                    isMouseOver={isMouseOver}
                    copyed={true}
                    text={row.url}
                    search={search}
                />
            </td>
            <td>
                <DataBlock
                    isMouseOver={isMouseOver}
                    copyed={true}
                    text={row.username}
                    search={search}
                />
            </td>
            <td>
                <DataBlock
                    isMouseOver={isMouseOver}
                    copyed={true}
                    hidden={true}
                    text={row.password}
                    search={search}
                />
            </td>
            <td>
                <DataBlock
                    isMouseOver={isMouseOver}
                    copyed={true}
                    text={row.note}
                    search={search}
                />
            </td>
        </tr>
    );
};

export default Trow;
