import { useMemo } from 'react';

import { TAppState } from "../types/table";

const useTablefilter = (table: TAppState['table'], search: string) => {
    const tableForShow = useMemo(() => {
        if (table === null) {
            return [];
        }
        
        if (search === '') {
            return table;
        }

        const tableFilter = table.filter((row) => {
            const searchText = search.toLocaleLowerCase();
            const values = Object.values(row);

            for (const value of values) {
                const valueText = `${value}`.toLocaleLowerCase();

                if (valueText.includes(searchText)) {
                    return true;
                }
            }
            return false;
        });

        return tableFilter;
    }, [
        search,
        table,
    ]);

    return tableForShow;
};

export default useTablefilter;
