import React from 'react';

import { TSort, TSortKey } from '../types/table';

import './index.css';

type TProps = {
    title: string;
    sortKey: TSortKey;
    sort: TSort;

    onClick: (key: TSortKey) => void;
};

const ThSort = (props: TProps) => {
    const {
        title,
        sortKey,
        sort,
        onClick,
    } = props;

    const active = sort.key === sortKey;

    const className = `ThSort ${active ? 'ThSort--active' : ''}`;

    return (
        <div
            className={className}
            onClick={() => {
                onClick(sortKey);
            }}
        >
            {title}
            {active && (
                <div className='ThSort__arr'>
                    {sort.dir === -1 ? (
                        <div>&uarr;</div>
                    ) : (
                        <div>&darr;</div>
                    )}
                </div>
            )}
        </div>
    );
};

export default ThSort;
