import CryptoJSAES from 'crypto-js/aes';
import CryptoJS from 'crypto-js';

export const encrypt = (key: string, text: string) => {
    const encryptText = CryptoJSAES.encrypt(text, key).toString();
    return encryptText;
};

export const decrypt = (key: string, encryptText: string) => {
    const bytes = CryptoJSAES.decrypt(encryptText, key);
    const text = bytes.toString(CryptoJS.enc.Utf8);
    return text;
};
