import React, { useState } from 'react';

import './index.css';

type TProps = {
    submit: (cryptKey: string) => void;
};

const InputKey = (props: TProps) => {
    const {
        submit,
    } = props;

    const [cryptKey, setCryptKey] = useState('');

    const [type, setType] = useState<'text' | 'password'>('password');

    return (
        <div className='InputKey'>
            <input
                type={type}
                value={cryptKey}
                onChange={(event) => {
                    const cryptKey = event.target.value;
                    setCryptKey(cryptKey);
                }}
            />
            <button
                onMouseDown={() => setType('text')}
                onMouseUp={() => setType('password')}
            >
                Eye
            </button>
            <button
                onClick={() => {
                    submit(cryptKey);
                }}
            >
                Submit
            </button>
        </div>
    );
};

export default InputKey;
