import React, { useState, useCallback } from 'react';

import copy from '../utils/copy';

import SearchData from '../SearchData';

import './index.css';

type TProps = {
    text: string;
    search?: string;

    isMouseOver: boolean;

    copyed?: boolean;
    hidden?: boolean;
    edit?: () => void;
};

const DataBlock = (props: TProps) => {
    const {
        text,
        search = '',

        isMouseOver,

        copyed = false,
        hidden = false,
        edit,
    } = props;

    const [noteCopy, setNoteCopy] = useState(false);
    const [hide, setHide] = useState(hidden);

    const onClickCopy = useCallback(() => {
        copy(text);
        setNoteCopy(true);
        window.setTimeout(() => {
            setNoteCopy(false);
        }, 1000);
    }, [
        text,
    ]);

    const onClickShow = useCallback(() => {
        if (hidden) {
            setHide(false);
            window.setTimeout(() => {
                setHide(true);
            }, 1000);
        }
    }, [
        hidden,
    ]);

    const showButtons = copyed || hidden || !!edit;

    const className = `${isMouseOver ? 'DataBlock DataBlock--isMouseOver' : 'DataBlock'}`;

    return (
        <div className={className}>
            <div>
                <SearchData
                    search={search}
                    text={text}
                    hide={hide}
                />
            </div>
            {showButtons && (
                <div className='DataBlock__buttons-wrap'>
                    <div className='DataBlock__buttons'>
                        {copyed && (
                            <button
                                className='DataBlock__button'
                                onClick={onClickCopy}
                            >
                                Copy
                            </button>
                        )}

                        {hidden && (
                            <button
                                className='DataBlock__button'
                                onClick={onClickShow}
                            >
                                Show
                            </button>
                        )}
                        {!!edit && (
                            <button
                                className='DataBlock__button'
                                onClick={edit}
                            >
                                Edit
                            </button>
                        )}
                    </div>
                </div>
            )}

            {noteCopy && (
                <div className='DataBlock__copyed'>
                    Copyed
                </div>
            )}
        </div>
    );
};

export default DataBlock;
